/**
 * PSDEvaluationTestTab -    
 * PersonalServiceDashboard 
 */
import React from 'react';
import Card from '../common/Card';
import Button from '../common/Button';
import type { UserProject } from '../../types';

interface PSDEvaluationTestTabProps {
  projects: UserProject[];
}

const PSDEvaluationTestTab: React.FC<PSDEvaluationTestTabProps> = ({ projects }) => (
  <div className="space-y-6">
    <div className="text-center py-8">
      <div className="mb-6">
        <div
          className="w-24 h-24 mx-auto rounded-full border-4 border-dashed flex items-center justify-center mb-4"
          style={{ borderColor: 'var(--accent-primary)' }}
        >
          <span className="text-4xl"></span>
        </div>
        <h3
          className="text-2xl font-bold mb-2"
          style={{ color: 'var(--text-primary)' }}
        >
           
        </h3>
        <p
          className="text-lg"
          style={{ color: 'var(--text-secondary)' }}
        >
              
        </p>
      </div>

      <div className="max-w-5xl mx-auto">
        <div className="grid grid-cols-1 lg:grid-cols-2 gap-8 mb-8">
          {/*    */}
          <Card
            title=" "
            icon=""
            className="p-6"
            style={{
              backgroundColor: 'var(--bg-secondary)',
              border: '2px solid var(--accent-primary)'
            }}
          >
            <div className="space-y-4">
              <p
                className="text-sm"
                style={{ color: 'var(--text-muted)' }}
              >
                  
              </p>
              <select
                className="w-full p-3 rounded-lg border-2"
                style={{
                  borderColor: 'var(--border-color)',
                  backgroundColor: 'var(--bg-elevated)'
                }}
              >
                <option value=""> ...</option>
                {(projects || []).filter(p => (p.criteria_count || 0) >= 3 && (p.alternatives_count || 0) >= 2).map((project, index) => (
                  <option key={index} value={project.id}>
                    {project.title}
                  </option>
                ))}
              </select>
            </div>
          </Card>

          {/*    */}
          <Card
            title=" "
            icon=""
            className="p-6"
            style={{
              backgroundColor: 'var(--bg-secondary)',
              border: '2px solid var(--accent-secondary)'
            }}
          >
            <div className="space-y-4">
              <p
                className="text-sm"
                style={{ color: 'var(--text-muted)' }}
              >
                   
              </p>
              <div className="space-y-2">
                <label className="flex items-center space-x-2">
                  <input
                    type="radio"
                    name="evaluator-role"
                    value="expert"
                    className="form-radio"
                  />
                  <span className="text-sm"> </span>
                </label>
                <label className="flex items-center space-x-2">
                  <input
                    type="radio"
                    name="evaluator-role"
                    value="stakeholder"
                    className="form-radio"
                  />
                  <span className="text-sm"></span>
                </label>
                <label className="flex items-center space-x-2">
                  <input
                    type="radio"
                    name="evaluator-role"
                    value="general"
                    className="form-radio"
                  />
                  <span className="text-sm"> </span>
                </label>
              </div>
            </div>
          </Card>

          {/*    */}
          <Card
            title=" "
            icon=""
            className="p-6"
            style={{
              backgroundColor: 'var(--bg-secondary)',
              border: '2px solid var(--status-info-bg)'
            }}
          >
            <div className="space-y-4">
              <p
                className="text-sm"
                style={{ color: 'var(--text-muted)' }}
              >
                  
              </p>
              <div className="space-y-2">
                <label className="flex items-center space-x-2">
                  <input
                    type="checkbox"
                    className="form-checkbox"
                  />
                  <span className="text-sm">  </span>
                </label>
                <label className="flex items-center space-x-2">
                  <input
                    type="checkbox"
                    className="form-checkbox"
                    defaultChecked
                  />
                  <span className="text-sm"> </span>
                </label>
                <label className="flex items-center space-x-2">
                  <input
                    type="checkbox"
                    className="form-checkbox"
                  />
                  <span className="text-sm"> </span>
                </label>
              </div>
            </div>
          </Card>
        </div>

        {/*    */}
        <div className="space-y-4">
          <Button
            variant="primary"
            size="lg"
            className="px-12 py-4 text-lg font-bold"
            style={{
              backgroundColor: 'var(--accent-primary)',
              color: 'white'
            }}
          >
               
          </Button>

          <div className="grid grid-cols-1 md:grid-cols-2 gap-6 text-center">
            <div className="p-4 rounded-lg" style={{ backgroundColor: 'var(--bg-elevated)' }}>
              <div className="text-2xl mb-2"></div>
              <h4 className="font-semibold mb-1"> </h4>
              <p className="text-sm" style={{ color: 'var(--text-muted)' }}>
                    
              </p>
            </div>
            <div className="p-4 rounded-lg" style={{ backgroundColor: 'var(--bg-elevated)' }}>
              <div className="text-2xl mb-2"></div>
              <h4 className="font-semibold mb-1"> </h4>
              <p className="text-sm" style={{ color: 'var(--text-muted)' }}>
                    
              </p>
            </div>
            <div className="p-4 rounded-lg" style={{ backgroundColor: 'var(--bg-elevated)' }}>
              <div className="text-2xl mb-2"></div>
              <h4 className="font-semibold mb-1"> </h4>
              <p className="text-sm" style={{ color: 'var(--text-muted)' }}>
                   
              </p>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
);

export default React.memo(PSDEvaluationTestTab);
